<template lang="html">

  <div class="tabNav" >
    <ul class="nav nav-tabs">
      <li v-for="(tab,i) in tabs" :key="tab" class="nav-item">
        <div class="navbar-brand">


        <a v-if="tabType" class="tab_link_comp" :class="{ active: tab === selected}" @click="setTab(tab)">
          {{ tab }}
        </a>

        <a v-if="!tabType&&i>=4" class="tab_link_employee" :class="{ active: tab === selected}" @click="setTab(tab)">
          {{ tab }}
        </a>

        <a v-if="!tabType&&i<4" class="tab_link_report" :class="{ active: tab === selected}" @click="setTab(tab)">
          {{ tab }}
        </a>



        </div>
      </li>
    </ul>

    <slot ></slot>

  </div>

</template>

<script>
export default {
  props: {
    tabs: {
      type: Array,
      required: true,
    },
    selected:{
      type: String,
      required: true,
    },
    tabType:{
      type:Boolean,
    }
  },
  methods: {
    setTab(tab){
      this.$emit('selected',tab);
    }
  },

}
</script>

<style lang="css" scoped>

.tabNav{
  padding-top: 15px;
}
.tab_link_comp{
  border-color: #000410;
  border: 10px;
  text-align: center;
  padding: 5px 15px;
  background-color: #003070;
  color: #ffffff;
  text-decoration: none;
  border-style: solid;
  border-color: black;
  border-width: medium;

}
.tab_link_employee{
  border-color: #000410;
  border: 10px;
  text-align: center;
  padding: 5px 15px;
  background-color: #007030;
  color: #ffffff;
  text-decoration: none;
  border-style: solid;
  border-color: black;
  border-width: medium;

}

.tab_link_report{
  border-color: #000410;
  border: 10px;
  text-align: center;
  padding: 5px 15px;
  background-color: #700030;
  color: #ffffff;
  text-decoration: none;
  border-style: solid;
  border-color: black;
  border-width: medium;

}
.tab_link_comp:hover{
  background-color: #000410;
}
.tab_link_comp:visited {
  background-color: #000033;
}

.tab_link_employee:hover{
  background-color: #000410;
}
.tab_link_employee:visited {
  background-color: #000033;
}
</style>
